.timeline-wrapper {
  margin-top: -28px;
}
.timeline-wrapper ul {
  padding-top: 28px;
  border-left: 5px solid #333;
}
.timeline-wrapper .timeline-title {
  font-size: 17px;
  font-weight: 400;
  line-height: 1.33;
  margin-bottom: 0;
  margin-left: 10px;
  padding: 2px 0 2px 22px;
  color: #fff;
  border-radius: 5px;
  background: #333;
}
.timeline-wrapper::after {
  float: left;
  width: 0;
  height: 0;
  margin-top: -23px;
  margin-left: 15px;
  content: '';
  border: 8px solid #333;
  border-radius: 100%;
}

